body {
  overflow: auto;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
}

.input-section {
  width: 100%;
  padding: 15px;
  margin-bottom: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.output-section {
  width: 100%;
  padding: 15px; 
  margin-bottom: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.input-section label {
  display: block;
  font-size: 18px;
  margin-bottom: 20px; 
}

.input-section input,
.input-section textarea {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
  box-sizing: border-box;
}

button {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  margin-top: 15px;
}

button:hover {
  background-color: #45a049;
}

.output-section {
  width: 10npm install gh-pages --save-dev0%;
  padding: 15px; 
  margin-bottom: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
  max-height: 600px;
  overflow-y: auto;
  text-align: left;
}

.output-section h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.output-section pre {
  white-space: pre-wrap;
  word-break: break-all;
  font-size: 16px; 
}